"use client";

import Link from 'next/link';
import Header from './components/Header';
import TableTransactions from './components/TableTransactions';
import CreateTransaction from './components/CreateTransaction';
import { Button } from '@/components/ui/button';
import {
  ResizablePanelGroup,
  ResizablePanel,
  ResizableHandle,
} from '@/components/ui/resizable';

export default function Home() {
  return (
    <div className="min-h-screen flex flex-col">
      <Header />
      <main className="pt-16 flex justify-center w-full">
        {/* Responsive direction switches layout based on container width */}
        <ResizablePanelGroup
          direction="horizontal"
          className="min-h-screen rounded-lg border border-gray-300 dark:border-gray-700 overflow-hidden flex-col md:flex-row"
        >
          <ResizablePanel
            defaultSize={60}
            minSize={50}
            maxSize={70}
            className="overflow-auto ml-4 mt-8"
          >
            <CreateTransaction />
          </ResizablePanel>

          <ResizableHandle withHandle />

          <ResizablePanel
            defaultSize={75}
            minSize={60}
            className="flex-1 p-4 overflow-auto"
          >
            <TableTransactions />
          </ResizablePanel>
        </ResizablePanelGroup>
      </main>
    </div>
  );
}
